.flex {
  display: flex;
  display: -webkit-flex;
}
#result #main #success {
  background-color: #fff;
  min-height: 100vh;
}
#result #main #success img {
  width: 100vw;
  height: 100vw;
}
#result #main #success .text {
  position: relative;
  top: -8rem;
  text-align: center;
  line-height: 1.7rem;
}
#result #main #success .text p,
#result #main #success .text h2 {
  margin: 0;
}
#result #main #success .text > p {
  font-size: 0.9rem;
  color: #bbbbbb;
}
#result #main #success .am-button {
  width: 90%;
  margin: 0 auto;
  margin-top: -3rem;
  color: white;
  font-size: 0.8rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#A34421), to(#6B2128));
  background-image: linear-gradient(#A34421, #6B2128);
}
#result #main #fail #top {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 5px 5px #dddddd;
  padding-bottom: 2rem;
}
#result #main #fail #top img {
  width: 75vw;
}
#result #main #fail #top span {
  margin-top: -0.8rem;
  color: #999999;
}
#result #main #fail #total {
  display: flex;
  display: -webkit-flex;
  align-items: center;
  background-color: #fff;
  margin-top: 2px;
  box-shadow: 0 5px 5px #dddddd;
  box-sizing: border-box;
  padding: 0.8rem 0.5rem;
}
#result #main #fail #total > p {
  margin: 0;
}
#result #main #fail #total > p:first-child {
  align-self: flex-start;
  margin-top: -2px;
  margin-right: 5px;
}
#result #main #fail #total > p:first-child img {
  width: 8vw;
  height: 8vw;
}
#result #main #fail #total > p:nth-child(2) {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  flex-grow: 1;
}
#result #main #fail #total > p:nth-child(2) > span:first-child {
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 0.4rem;
}
#result #main #fail #total > p:nth-child(2) > span:nth-child(2) {
  color: #999999;
  font-size: 0.75rem;
}
#result #main #fail #total > p:nth-child(3) .am-button {
  color: white;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#A34421), to(#6B2128));
  background-image: linear-gradient(#A34421, #6B2128);
  border-radius: 2em;
}
#result #main #fail #failList {
  background-color: #fff;
  margin-top: 2px;
  padding: 1rem;
}
#result #main #fail #failList h3,
#result #main #fail #failList p {
  margin: 0;
}
#result #main #fail #failList .list {
  margin-top: 1rem;
}
#result #main #fail #failList .list > div {
  display: flex;
  display: -webkit-flex;
}
#result #main #fail #failList .list > div > p:first-child span {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  height: 1.2rem;
  width: 1.2rem;
  border: 1px solid #6C2127;
  color: #6C2127;
  border-radius: 50%;
}
#result #main #fail #failList .list > div > p:nth-child(2) {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  margin-left: 0.5rem;
  flex-grow: 1;
}
#result #main #fail #failList .list > div > p:nth-child(2) > span:first-child {
  font-size: 0.95rem;
  font-weight: bold;
  margin-bottom: 0.4rem;
}
#result #main #fail #failList .list > div > p:nth-child(2) > span:nth-child(2) {
  font-size: 0.8rem;
  color: #999999;
}
#result #main #fail #failList .list > div:not(:last-child) {
  margin-bottom: 1rem;
}
#result #main #fail #failList .list > div:not(:last-child) > p:nth-child(2) {
  padding-bottom: 1rem;
  border-bottom: 1px solid #DDDDDD;
}
