.flex {
  display: flex;
}
#exam {
  background-color: #fff;
  height: 100vh;
}
#exam #main {
  padding: 0 1rem;
}
#exam #main p {
  margin: 0;
}
#exam #main .am-checkbox-inner {
  border-color: #6C2127;
}
#exam #main .am-checkbox.am-checkbox-checked .am-checkbox-inner {
  background: #6C2127;
}
#exam #main > div:first-child {
  text-align: center;
  font-weight: bold;
  margin: 2rem 0;
  font-size: 1.1rem;
}
#exam #main > div:nth-child(2) > p:first-child {
  display: inline-block;
  width: 5rem;
  text-align: center;
  height: 2rem;
  line-height: 1.9rem;
  border: 1.5px solid #6C2127;
  color: #6C2127;
  border-radius: 5px;
  box-sizing: border-box;
  margin-right: 0.5rem;
  margin-bottom: 0.2rem;
}
#exam #main > div:nth-child(2) > p:nth-child(2) {
  display: inline;
  font-weight: bold;
  color: #333333;
  font-size: 1rem;
  line-height: 1.7rem;
}
#exam #main #submit {
  margin-top: 2rem;
}
#exam #main #submit .am-button {
  color: white;
  font-size: 0.8rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#A34421), to(#6B2128));
  background-image: linear-gradient(#A34421, #6B2128);
}
#exam #main #submit .am-button-active {
  opacity: 0.85;
}
