#home {
  background-image: url(../../static/media/bg_img_home.1d05be5b.png);
  background-size: 100% 65%;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  color: white;
}
#home .am-navbar {
  background-color: transparent;
}
#home #letter {
  margin-top: 2.5rem;
  padding: 0 1rem;
}
#home #letter > p {
  line-height: 1.4rem;
  letter-spacing: 1.5px;
}
#home #rule {
  width: 97.2vw;
  height: 51.46666667vw;
  margin: 0 auto;
  background-image: url(../../static/media/rule_bg.f155f385.png);
  background-size: 100% 100%;
  box-sizing: border-box;
  padding-left: 8.26666667vw;
  padding-top: 11.33333333vw;
  padding-right: 8.26666667vw;
}
#home #rule h3,
#home #rule p {
  margin: 0;
}
#home #rule > h3 {
  margin-bottom: 0.5rem;
}
#home #rule > p {
  line-height: 1.35rem;
}
#home #start {
  padding: 0 1rem;
  margin-top: 2rem;
}
#home #start .am-button {
  color: white;
  font-size: 0.8rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#A34421), to(#6B2128));
  background-image: linear-gradient(#A34421, #6B2128);
}
#home #start .am-button-active {
  opacity: 0.85;
}
